<template>
<div id="app">
    <!-- Navbar -->
    <b-navbar toggleable="md" type="dark" variant="dark">
        <b-navbar-brand href="#">Smart Attendance</b-navbar-brand>

        <b-collapse is-nav id="nav_collapse">
            <b-navbar-nav>
                <b-nav-item href="#" @click="navigate('dashboard')" :active="page == 'dashboard'">Dashboard</b-nav-item>
                <b-nav-item href="#" @click="navigate('classes')" :active="page == 'classes' || page == 'selected-class'">Turmas</b-nav-item>
                <b-nav-item href="#" @click="navigate('subjects')" :active="page == 'subjects'">Disciplinas</b-nav-item>
                <b-nav-item href="#" @click="navigate('students')" :active="page == 'students'">Alunos</b-nav-item>
            </b-navbar-nav>
        </b-collapse>

        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
    </b-navbar>

    <!-- Dashboard -->
    <div class="container v-center py-4" v-if="page == 'dashboard'">
        <div class="row">
            <div class="col-sm mb-5 mb-md-0">
                <h4>Disciplinas</h4>
                <b-list-group>
                    <b-list-group-item v-for="item in subjects" v-bind:key="item.id" class="d-flex justify-content-between align-items-center">
                        {{ item.name }}
                    </b-list-group-item>
                </b-list-group>
            </div>
            <div class="col-sm">
                <h4>Aulas agora</h4>
                <b-list-group>
                    <b-list-group-item href="#" v-for="item in now" v-bind:key="item.roomID" class="d-flex justify-content-between align-items-center" @click="selectedClassIndex=classes.indexOf(classes.filter(x => x.id == item.classID && x.subjectID == item.subjectID)[0]) ; navigate('selected-class')">
                        {{ item.name + ' - Sala ' + item.roomID }}
                        <span class="badge badge-primary badge-pill">{{ item.classID }}</span>
                    </b-list-group-item>
                </b-list-group>
            </div>
        </div>
    </div>

    <!-- Classes -->
    <div class="container v-center py-4" v-if="page == 'classes'">
        <div class="row">
            <div class="col-sm-8 col-md-9 mb-5 mb-md-0">
                <h4>Turmas</h4>
                <b-list-group>
                    <b-list-group-item href="#" v-for="item in classes" v-bind:key="item.id + '_' + item.subjectID" class="d-flex justify-content-between align-items-center" @click="selectedClassIndex=classes.indexOf(item) ; navigate('selected-class')">
                        {{ item.name }}
                        <span class="badge badge-primary badge-pill">{{ item.id }}</span>
                    </b-list-group-item>
                </b-list-group>
            </div>
            <div class="col-sm-3">
                <h4>Aes</h4>
                <div class="d-flex flex-column">
                    <b-button class="mx-auto mb-1" variant="primary" @click="showRegisterClass">Cadastrar nova</b-button>
                </div>
            </div>
        </div>
    </div>

    <!-- Subjects -->
    <div class="container v-center py-4" v-if="page == 'subjects'">
        <div class="row">
            <div class="col-sm-8 col-md-9 mb-5 mb-md-0">
                <h4>Disciplinas</h4>
                <b-list-group>
                    <b-list-group-item v-for="item in subjects" v-bind:key="item.id" class="d-flex justify-content-between align-items-center">
                        {{ item.name }}
                    </b-list-group-item>
                </b-list-group>
            </div>
            <div class="col-sm-3">
                <h4>Aes</h4>
                <div class="d-flex flex-column">
                    <b-button class="mx-auto mb-1" variant="primary" @click="showRegisterSubject">Cadastrar nova</b-button>
                </div>
            </div>
        </div>
    </div>

    <!-- Students -->
    <div class="container v-center py-4" v-if="page == 'students'">
        <div class="row">
            <div class="col-sm-8 col-md-9 mb-5 mb-md-0">
                <h4>Alunos</h4>
                <b-list-group>
                    <b-list-group-item v-for="item in students" v-bind:key="item.id" class="d-flex flex-column align-items-start">
                        <div v-b-toggle="item.id + '_collapse'" class="w-100 d-flex flex-column align-items-start">
                            <div class="d-flex w-100 justify-content-between">
                                <h5 class="mb-1">{{ item.name }}</h5>
                                <small>{{ item.classes.length }} {{ item.classes.length == 1 ? 'matria' : 'matrias' }}</small>
                            </div>
                            <small>Clique para ver mais</small>
                        </div>
                        <b-collapse :id="item.id + '_collapse'" class="w-100 text-left" accordion="students-accordion">
                            <hr>
                            <ul class="mb-3">
                                <li class="d-block" v-for="subject in item.classes" v-bind:key="subject.subjectID"><span>{{ subject.name }}</span> <span class="badge badge-primary badge-pill ml-2">{{ subject.id }}</span></li>
                            </ul>
                            <div class="row">
                                <div class="col-12 col-md-6 mb-1">
                                    <b-button size="sm" variant="primary" class="w-100" @click="showRegisterStudentClass(item.id)">Adicionar disciplina</b-button>
                                </div>
                                <div class="col-12 col-md-6">
                                    <b-button size="sm" variant="outline-secondary" class="w-100" @click="showRegisterStudentPicture(item.id)">Adicionar foto</b-button>
                                </div>
                            </div>
                        </b-collapse>
                    </b-list-group-item>
                </b-list-group>
            </div>
            <div class="col-sm-3">
                <h4>Aes</h4>
                <div class="d-flex flex-column">
                    <b-button class="mx-auto mb-1" variant="primary" @click="showRegisterStudent">Cadastrar novo</b-button>
                </div>
            </div>
        </div>
    </div>

    <!-- Selected class -->
    <div class="container v-center py-4" v-if="page == 'selected-class'">
        <div class="row">
            <div class="col-12 mb-5 mb-md-0">
                <h3 class="mb-5">
                    {{ selectedClass.name }} - {{ selectedClass.id }}
                    <br>
                    <small>Sala {{ selectedClass.roomID }} - {{ new Date() | moment('DD/MM/YYYY') }}</small>
                </h3>
                
                <h4 class="mb-4">Alunos presentes</h4>
                <div v-if="selectedClassStudents.length == 0">
                    No h alunos cadastrados
                </div>
                <div v-else class="row d-flex align-items-center justify-content-center">
                    <div v-for="student in selectedClassStudents" v-bind:key="student.id" class="square-col border mx-1 my-1 px-2 d-flex justify-content-center align-items-center" v-bind:class="{'bg-primary': selectedClassAttendance.indexOf(student.id) != -1, 'text-white': selectedClassAttendance.indexOf(student.id) != -1, 'border-primary': selectedClassAttendance.indexOf(student.id) != -1}">
                        {{ student.name }}
                    </div>
                </div>
            </div>
            <!-- <div class="col-sm-3">
                <h4>Aes</h4>
                <div class="d-flex flex-column">
                    <b-button class="mx-auto mb-1" variant="primary" @click="showRegisterClass">Cadastrar nova</b-button>
                </div>
            </div> -->
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "app",
    data() {
        return {
            page: 'dashboard',
            selectedClassIndex: -1,
            subjects: [
                { name: 'Programao Orientada  Objetos', id: '1' },
                { name: 'Arquitetura de Computadores', id: '2' },
                { name: 'Circuitos Digitais', id: '3' },
                { name: 'Estruturas de Dados e Algortimos', id: '4' },
            ],
            now: [
                { name: 'Programao Orientada  Objetos', roomID: '302', classID: 'A1', subjectID: '1' },
                { name: 'Estruturas de Dados e Algortimos', roomID: '204', classID: 'AA', subjectID: '4' },
            ],
            classes: [
                { name: 'Programao Orientada  Objetos', roomID: '302', id: 'A1', subjectID: '1', students: ['117031010']},
                { name: 'Estruturas de Dados e Algortimos', roomID: '204', id: 'AA', subjectID: '4', students: ['116031014'] },
            ],
            students: [
                { name: 'Patrick Pissurno', id: '117031010', classes: [ { name: 'Programao Orientada  Objetos', roomID: '302', id: 'AA', subjectID: '1' }, { name: 'Arquitetura de Computadores', roomID: '204', id: 'A1', subjectID: '2' } ]},
                { name: 'Luciano Hanna', id: '116031014', classes: [ { name: 'Estrutura de Dados e Algortimos', roomID: '213', id: 'AA', subjectID: '4' }, { name: 'Arquitetura de Computadores', roomID: '204', id: 'A1', subjectID: '2' } ]},
            ],
            _interval: null
        };
    },
    computed: {
        selectedClass(){
            if(this.selectedClassIndex == null || this.selectedClassIndex == -1)
                return null;
            return this.classes[this.selectedClassIndex];
        },
        selectedClassStudents(){
            if(!this.selectedClass || !this.selectedClass.students)
                return [];
            return this.selectedClass.students.map(c => {
                let arr = this.students.filter(s => s.id == c);
                return arr.length == 0 ? null : arr[0];
            }).filter(x => x != null);
        }
    },
    asyncComputed: {
        selectedClassAttendance: {
            async get(){
                if(this.selectedClassIndex == -1)
                    return [];

                console.log(this.selectedClassIndex);
                return ['117031010'];
            },
            default: []
        }
    },
    mounted(){
        this._interval = setInterval(() => {
            if(this.selectedClassIndex != -1){
                this.selectedClassIndex += 1;
                this.selectedClassIndex -= 1;
            }
        }, 5000);
    },
    destroyed(){
        clearInterval(this._interval);
    },
    methods: {
        async navigate(page){
            this.page = page;
            if(page != 'selected-class')
                this.selectedClassIndex = -1;
        },
        async showRegisterSubject(){
            let result = await this.$swal({
                title: 'Cadastrar disciplina',
                html: ` <input id="swal-input1" placeholder="Nome" class="swal2-input">
                        <input id="swal-input2" placeholder="Cdigo" class="swal2-input">`,
                focusConfirm: false,
                preConfirm: async () => {
                    let nome = document.getElementById('swal-input1').value.trim();
                    let codigo = document.getElementById('swal-input2').value.toUpperCase().trim();
                    if(nome.length < 3)
                        return this.$swal.showValidationError('Insira um nome vlido') && false;
                    if(codigo.length < 4)
                        return this.$swal.showValidationError('Insira um cdigo vlido') && false;
                    return [ nome, codigo ];
                }
            });

            if(!result || !result.value)
                return;

            result = result.value;

            this.subjects.push({
                name: result[0],
                id: result[1]
            });
        },
        async showRegisterClass(){
            let result = await this.$swal({
                title: 'Cadastrar disciplina',
                html: ` <div class="form-group">
                            <select id="swal-input1" placeholder="Nome" class="form-control mt-4 mb-3">${
                                this.subjects.map(x => `<option value="${x.id}">${x.name}</option>`).join('')
                            }</select>
                            <input id="swal-input2" placeholder="Nome" class="form-control">
                        </div>`,
                focusConfirm: false,
                preConfirm: async () => {
                    let subjectID = document.getElementById('swal-input1').value.trim();
                    let nome = document.getElementById('swal-input2').value.toUpperCase().trim();
                    if(this.subjects.filter(x => x.id == subjectID).length == 0)
                        return this.$swal.showValidationError('Selecione uma disciplina vlida') && false;
                    if(nome.length < 2)
                        return this.$swal.showValidationError('Insira um nome vlido') && false;
                    return [ subjectID, nome ];
                }
            });

            if(!result || !result.value)
                return;

            result = result.value;

            let subject = this.subjects.filter(x => x.id == result[0])[0];

            this.classes.push({
                name: subject.name,
                subjectID: subject.id,
                id: result[1]
            });
        },
        async showRegisterStudent(){
            let result = await this.$swal({
                title: 'Cadastrar aluno',
                html: `
                        <input id="swal-input1" placeholder="Nome" class="swal2-input">
                        <input id="swal-input2" placeholder="Matrcula" class="swal2-input">
                        `,
                focusConfirm: false,
                preConfirm: async () => {
                    let nome = document.getElementById('swal-input1').value.trim();
                    let matricula = document.getElementById('swal-input2').value.trim();
                    if(nome.length < 5)
                        return this.$swal.showValidationError('Digite o nome completo') && false;
                    if(matricula.length != 9)
                        return this.$swal.showValidationError('Insira uma matrcula vlida') && false;
                    return [ nome, matricula ];
                }
            });

            if(!result || !result.value)
                return;

            result = result.value;

            this.students.push({
                name: result[0],
                classes: [],
                id: result[1]
            });
        },
        async showRegisterStudentClass(studentID){
            let result = await this.$swal({
                title: 'Adicionar disciplina',
                html: ` <div class="form-group">
                            <select id="swal-input1" placeholder="Disciplina" class="form-control mt-4 mb-3">${
                                this.classes.map(x => `<option value="${x.id + ';' + x.subjectID}">${x.name} - ${x.id}</option>`).join('')
                            }</select>
                        </div>`,
                focusConfirm: false,
                preConfirm: async () => {
                    let data = document.getElementById('swal-input1').value.trim().split(';');
                    if(this.classes.filter(x => x.subjectID == data[1] && x.id == data[0]).length == 0)
                        return this.$swal.showValidationError('Selecione uma disciplina vlida') && false;
                    return data;
                }
            });

            if(!result || !result.value)
                return;

            result = result.value;

            let student = this.students.filter(x => x.id == studentID)[0];
            let subject = this.classes.filter(x => x.subjectID == result[1] && x.id == result[0])[0];
            student.classes.push(subject);
        },
        async showRegisterStudentPicture(studentID){
            let result = await this.$swal({
                title: 'Adicionar foto',
                html: ` <div class="form-group mt-3 text-left">
                            <label for="swal-input1">Selecione o arquivo</label>
                            <input type="file" class="form-control-file" id="swal-input1">
                        </div>`,
                focusConfirm: false,
                preConfirm: async () => {
                    let data = document.getElementById('swal-input1').files[0];
                    if(!data)
                        return this.$swal.showValidationError('Selecione uma foto vlida') && false;
                    return data;
                }
            });

            if(!result || !result.value)
                return;

            result = result.value;
        }
    }
};
</script>

<style lang="scss">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.v-center {
    min-height: calc(100vh - 56px);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.square-col {
    min-width: 7rem;
    min-height: 7rem;
    max-width: 7rem;
    max-height: 7rem;
    -ms-flex: 0 0 7rem;
    flex: 0 0 7rem;
}
</style>



